import * as THREE from 'three';
import { Capsule } from 'three/examples/jsm/math/Capsule.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

export class PlayerController {
  constructor(scene, worldOctree, avatarInfo, initCallback) {
    console.log('player controller', avatarInfo);
    this.scene = scene;
    this.worldOctree = worldOctree;
    this.model = null;
    this.mixer = null;
    this.animationsMap = new Map();
    // 
    this.initialPosition = avatarInfo?.player.position
      ? new THREE.Vector3(
        avatarInfo.player.position.x,
        avatarInfo.player.position.y,
        avatarInfo.player.position.z,
      )
      : new THREE.Vector3(0, 0, 0);
    this.initialRotation = avatarInfo?.player.rotation
      ? new THREE.Vector3(
        avatarInfo.player.rotation.x,
        avatarInfo.player.rotation.y,
        avatarInfo.player.rotation.z,
      )
      : new THREE.Vector3(0, 0, 0);

    // 
    this.playerVelocity = new THREE.Vector3();
    this.playerOnFloor = true; // true
    this.gravity = 30;

    // 
    this.runVelocity = 5;
    this.walkVelocity = 2;
    this.toggleRun = true;

    // 
    this.currentAction = 'Idle';
    this.fadeDuration = 0.2;

    // 
    this.walkDirection = new THREE.Vector3();
    this.rotateAngle = new THREE.Vector3(0, 1, 0);
    this.rotateQuarternion = new THREE.Quaternion();

    //  - 
    this.playerCollider = new Capsule(
      new THREE.Vector3(
        this.initialPosition.x,
        this.initialPosition.y + 0.35,
        this.initialPosition.z,
      ),
      new THREE.Vector3(
        this.initialPosition.x,
        this.initialPosition.y + 1,
        this.initialPosition.z,
      ),
      0.35,
    );

    // 
    this.ensurePlayerOnGround();
  }
  async init() {
    await this.loadCharacterModel();
    console.log('loadCharacterModel', this.initialPosition);
    // 
    // this.setPositionAndRotation(this.initialPosition, this.initialRotation);
  }

  loadCharacterModel() {
    return new Promise((resolve, reject) => {
      const loader = new GLTFLoader();

      loader.setPath('./models/');
      loader.load(
        'Soldier.glb',
        (gltf) => {
          this.model = gltf.scene;
          this.model.traverse((object) => {
            if (object.isMesh) object.castShadow = true;
          });

          // 
          if (this.initialPosition) {
            this.model.position.copy(this.initialPosition);
            // 
            this.playerCollider.start.set(
              this.initialPosition.x,
              this.initialPosition.y + 0.35,
              this.initialPosition.z,
            );
            this.playerCollider.end.set(
              this.initialPosition.x,
              this.initialPosition.y + 1,
              this.initialPosition.z,
            );
          }
          if (this.initialRotation) {
            this.model.rotation.x = this.initialRotation.x;
            this.model.rotation.y = this.initialRotation.y;
            this.model.rotation.z = this.initialRotation.z;
          }

          this.scene.add(this.model);

          const gltfAnimations = gltf.animations;
          this.mixer = new THREE.AnimationMixer(this.model);
          gltfAnimations
            .filter(a => a.name !== 'TPose')
            .forEach((a) => {
              this.animationsMap.set(a.name, this.mixer.clipAction(a));
            });

          // 
          this.animationsMap.forEach((value, key) => {
            if (key === this.currentAction) {
              value.play();
            }
          });

          resolve(this);
        },
        undefined,
        reject,
      );
    });
  }

  isLoaded() {
    return this.model !== null && this.mixer !== null;
  }

  switchRunToggle() {
    this.toggleRun = !this.toggleRun;
  }

  // 
  ensurePlayerOnGround() {
    // 
    const startPosition = new THREE.Vector3(0, 10, 0); // 
    const rayDirection = new THREE.Vector3(0, -1, 0); // 

    // 
    this.playerCollider.start.copy(startPosition);
    this.playerCollider.end.copy(new THREE.Vector3(0, startPosition.y + 0.65, 0));

    // 
    this.playerVelocity.set(0, -0.1, 0);
  }

  update(delta, inputController, camera) {
    if (!this.mixer) return; // 

    const directionPressed = inputController.isDirectionPressed();

    // Shift
    // 
    const mobileRunState = inputController.getRunToggleState();
    const shouldRun = inputController.mobileController.isMobileDevice()
      ? mobileRunState
      : this.toggleRun;

    // 
    let play = '';
    if (directionPressed && shouldRun) {
      play = 'Run';
    } else if (directionPressed) {
      play = 'Walk';
    } else {
      play = 'Idle';
    }

    // 
    if (this.currentAction !== play) {
      const toPlay = this.animationsMap.get(play);
      const current = this.animationsMap.get(this.currentAction);

      if (toPlay && current) {
        current.fadeOut(this.fadeDuration);
        toPlay.reset().fadeIn(this.fadeDuration)
          .play();
        this.currentAction = play;
      }
    }

    this.mixer.update(delta);

    //  - 
    if (this.playerOnFloor) {
      this.playerVelocity.y = Math.max(0, this.playerVelocity.y);
    } else {
      this.playerVelocity.y -= this.gravity * delta;
    }

    // 
    if (this.currentAction === 'Run' || this.currentAction === 'Walk') {
      this.handleMovement(inputController, camera);
    } else {
      // 
      this.playerVelocity.x -= this.playerVelocity.x * 10.0 * delta;
      this.playerVelocity.z -= this.playerVelocity.z * 10.0 * delta;
    }

    // 
    this.updatePosition(delta);
  }

  handleMovement(inputController, camera) {
    // 
    const angleYCameraDirection = Math.atan2(
      camera.position.x - this.model.position.x,
      camera.position.z - this.model.position.z,
    );

    // 
    const directionOffset = inputController.getDirectionOffset();

    // 
    this.rotateQuarternion.setFromAxisAngle(
      this.rotateAngle,
      angleYCameraDirection + directionOffset,
    );
    this.model.quaternion.rotateTowards(this.rotateQuarternion, 0.2);

    // 
    camera.getWorldDirection(this.walkDirection);
    this.walkDirection.y = 0;
    this.walkDirection.normalize();
    this.walkDirection.applyAxisAngle(this.rotateAngle, directionOffset);

    // 
    const velocity =      this.currentAction === 'Run' ? this.runVelocity : this.walkVelocity;
    this.playerVelocity.x = this.walkDirection.x * velocity;
    this.playerVelocity.z = this.walkDirection.z * velocity;
  }

  updatePosition(delta) {
    if (!this.model) return; // 

    const deltaPosition = this.playerVelocity.clone().multiplyScalar(delta);
    this.playerCollider.translate(deltaPosition);

    // 
    this.playerOnFloor = false;
    const result = this.worldOctree.capsuleIntersect(this.playerCollider);

    if (result) {
      this.playerOnFloor = result.normal.y > 0;

      if (!this.playerOnFloor) {
        this.playerVelocity.addScaledVector(
          result.normal,
          -result.normal.dot(this.playerVelocity),
        );
      }

      this.playerCollider.translate(result.normal.multiplyScalar(result.depth));
    }

    // 
    if (this.playerCollider.end.y < 0.35) {
      this.playerCollider.end.y = 0.35;
      this.playerCollider.start.y = 0.35 - 0.65;
      this.playerOnFloor = true;
      this.playerVelocity.y = 0;
    }

    // 
    this.model.position.copy(this.playerCollider.end);
    this.model.position.y -= 1; // 

    // 
    this.initialPosition.copy(this.model.position);
  }

  getPosition() {
    return this.model ? this.model.position.clone() : new THREE.Vector3();
  }

  getVelocity() {
    return this.playerVelocity.clone();
  }

  getRotation() {
    return this.model ? this.model.rotation.clone() : new THREE.Euler();
  }

  setPosition(x, y, z) {
    if (!this.model) return;

    // 
    this.model.position.set(x, y, z);

    // 
    this.playerCollider.start.set(x, y + 0.35, z);
    this.playerCollider.end.set(x, y + 1, z);

    // 
    this.initialPosition.set(x, y, z);

    // 
    this.playerVelocity.set(0, 0, 0);

    console.log(':', { x, y, z });
  }

  setRotation(x, y, z) {
    if (!this.model) return;

    // 
    this.model.rotation.set(x, y, z);

    console.log(':', { x, y, z });
  }

  // 
  setPositionAndRotation(position, rotation) {
    position && this.setPosition(position.x, position.y, position.z);
    rotation && this.setRotation(rotation.x, rotation.y, rotation.z);
  }

  /**
   * 
   * @param {Object} sceneController - 
   * @param {string} modelUrl - URL
   * @returns {boolean} 
   */
  saveCurrentPosition(sceneController, modelUrl = 'default') {
    if (!this.model || !sceneController) {
      console.warn('PlayerControllerSceneController');
      return false;
    }

    try {
      // 
      const playerPosition = this.getPosition();
      const playerRotation = this.getRotation();

      // 
      const camera = sceneController.getCamera();
      const orbitControl = sceneController.getOrbitControl();

      const cameraInfo = {
        position: {
          x: camera.position.x,
          y: camera.position.y,
          z: camera.position.z,
        },
        target: {
          x: orbitControl.target.x,
          y: orbitControl.target.y,
          z: orbitControl.target.z,
        },
        polarAngle: orbitControl.getPolarAngle(),
        azimuthalAngle: orbitControl.getAzimuthalAngle(),
        distance: orbitControl.getDistance(),
      };

      // 
      const saveData = {
        timestamp: Date.now(),
        modelUrl,
        player: {
          position: {
            x: playerPosition.x,
            y: playerPosition.y,
            z: playerPosition.z,
          },
          rotation: {
            x: playerRotation.x,
            y: playerRotation.y,
            z: playerRotation.z,
          },
        },
        camera: cameraInfo,
      };

      // localStorage
      const storageKey = `metaverse_position_${modelUrl}`;
      localStorage.setItem(storageKey, JSON.stringify(saveData));

      console.log(':', saveData, JSON.stringify(saveData));
      return true;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }

  /**
   * 
   * @param {Object} sceneController - 
   * @param {string} modelUrl - URL
   * @returns {boolean} 
   */
  restoreSavedPosition(sceneController, modelUrl = 'default') {
    if (!this.model || !sceneController) {
      console.warn('PlayerControllerSceneController');
      return false;
    }

    try {
      const storageKey = `metaverse_position_${modelUrl}`;
      const savedDataStr = localStorage.getItem(storageKey);

      if (!savedDataStr) {
        console.log('');
        return false;
      }

      const savedData = JSON.parse(savedDataStr);
      console.log(':', savedData);

      savedData?.player
        && this.setPositionAndRotation(
          savedData.player?.position,
          savedData.player?.rotation,
        );

      // 
      savedData?.camera
        && sceneController.setCameraAndOribitControl(savedData.camera);
      // const camera = sceneController.getCamera();
      // camera.position.set(
      //   savedData.camera.position.x,
      //   savedData.camera.position.y,
      //   savedData.camera.position.z,
      // );
      // const orbitControl = sceneController.getOrbitControl();

      // // 
      // orbitControl.target.set(
      //   savedData.camera.target.x,
      //   savedData.camera.target.y,
      //   savedData.camera.target.z,
      // );
      // orbitControl.azimuthalAngle = savedData.camera.azimuthalAngle;
      // orbitControl.polarAngle = savedData.camera.distance;
      // orbitControl.distance = savedData.camera.polarAngle;

      // // OrbitControls
      // orbitControl.update();

      return true;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }
  /**
   * 
   * @param {string} modelUrl - URL
   * @returns {boolean} 
   */
  hasSavedPosition(modelUrl = 'default') {
    const storageKey = `metaverse_position_${modelUrl}`;
    return localStorage.getItem(storageKey) !== null;
  }

  /**
   * 
   * @param {string} modelUrl - URL
   * @returns {boolean} 
   */
  clearSavedPosition(modelUrl = 'default') {
    try {
      const storageKey = `metaverse_position_${modelUrl}`;
      localStorage.removeItem(storageKey);
      console.log('');
      return true;
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }
}
