<template>
  <div class="three-container" ref="threeContainer"></div>
  <button class="debug-button" @click="toggleDebug">
    {{ debugEnabled ? "" : "" }}
  </button>

  <!--  -->
  <div v-if="showDialog" class="dialog-container" :style="dialogPosition">
    <div class="dialog-box">
      <p class="dialog-text">{{ displayedText }}</p>
      <button
        v-if="showPurchaseButton"
        class="purchase-button"
        @click="handleInteraction"
      >
        
      </button>
    </div>
  </div>
  <div class="controls-guide" v-if="!isMobile">
    <div class="guide-title"></div>
    <div class="guide-item">
      <span class="key">W A S D</span>
      <span class="description"></span>
    </div>
    <div class="guide-item">
      <span class="key">SHIFT</span>
      <span class="description">/</span>
    </div>
  </div>
  <!-- <SuccessDialog v-model:visible="show" message="" /> -->
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { MetaverseViewer } from "../infranstructure/viewers/metaverse/index.js";
import { useRoute } from "vue-router";
import { getScreenOrientation } from "../utils/index.js";
// 
let lastHiddenTime = 0;
let visibilityTimeout = 10000; // 10

const threeContainer = ref(null);
let metaverse = null;
const route = useRoute();
console.log("route.query", route.query);
const modelUrl = ref(route.query.modelUrl);
const debugEnabled = ref(route.query.debug === "true");
const interactionEnabled = ref(route.query.interaction === "true");

let geometry = null;
let geometryPos = null;

if (route.query.geometry && route.query.geometryPos) {
  geometry = route.query.geometry.split("|");
  geometryPos = route.query.geometryPos.split("|");
}
if (route.query.shareName) {
  document.title = route.query.shareName;
}

// 
const showDialog = ref(false);
const fullText = "~";
const displayedText = ref("");
const showPurchaseButton = ref(false);
const dialogPosition = ref(null);

let closeDialog = false;
let isInZoneOrigin = false;

// 
let textAnimationInterval = null;
let currentCharIndex = 0;

// 
const isMobile = ref(true);

// 
const animateText = () => {
  if (currentCharIndex < fullText.length) {
    displayedText.value = fullText.substring(0, currentCharIndex + 1);
    currentCharIndex++;
  } else {
    clearInterval(textAnimationInterval);
    // 
    setTimeout(() => {
      showPurchaseButton.value = true;
    }, 500);
  }
};

// 
const startTextAnimation = () => {
  // 
  currentCharIndex = 0;
  displayedText.value = "";
  showPurchaseButton.value = false;

  // 
  if (textAnimationInterval) {
    clearInterval(textAnimationInterval);
  }

  // 
  textAnimationInterval = setInterval(animateText, 100); // 100
};

const initIeration = () => {
  metaverse.setInteractionCallback((isInZone) => {
    if (closeDialog && isInZone && isInZoneOrigin !== isInZone) return;
    if (isInZone === isInZoneOrigin) return;
    if (isInZone && !showDialog.value) {
      // 
      showDialog.value = true;
      // 
      startTextAnimation();
    } else if (!isInZone && showDialog.value) {
      // 
      showDialog.value = false;
    }
    isInZoneOrigin = isInZone;
    closeDialog = false;
  });
};

// 
const preventLongPress = (event) => {
  event.preventDefault();
  event.stopPropagation();
  return false;
};

// 
const handleVisibilityChange = () => {
  if (document.hidden) {
    // 
    console.log("");
    if (metaverse && metaverse.playerController && metaverse.sceneController) {
      metaverse.playerController.saveCurrentPosition(
        metaverse.sceneController,
        "default",
      );
    }
  } else {
    // 
    console.log("");
    setTimeout(() => {
      if (
        metaverse &&
        metaverse.playerController &&
        metaverse.sceneController
      ) {
        metaverse.playerController.restoreSavedPosition(
          metaverse.sceneController,
          "default",
        );
      }
    }, 200);
  }
};

// Metaverse
onMounted(async () => {
  // 
  document.addEventListener("visibilitychange", handleVisibilityChange);

  try {
    if (!modelUrl.value) return;
    metaverse = new MetaverseViewer(
      threeContainer.value,
      decodeURIComponent(modelUrl.value),
      {
        showInteraction: interactionEnabled.value,
        geometry,
        geometryPos,
      },
    );
    await metaverse.init();
    // 
    isMobile.value = metaverse.getIsMobile();
    metaverse.setDebugPanelVisible(debugEnabled.value);

    // 
    interactionEnabled.value && initIeration();

    // 
    window.addEventListener("resize", onWindowResize);

    // 
    if (threeContainer.value) {
      // 
      threeContainer.value.addEventListener("contextmenu", preventLongPress);

      // 
      threeContainer.value.addEventListener("selectstart", preventLongPress);

      // 
      threeContainer.value.addEventListener("dragstart", preventLongPress);

      // 
      let longPressTimer = null;

      threeContainer.value.addEventListener("touchstart", (e) => {
        longPressTimer = setTimeout(() => {
          e.preventDefault();
          e.stopPropagation();
        }, 500); // 500ms
      });

      threeContainer.value.addEventListener("touchend", () => {
        if (longPressTimer) {
          clearTimeout(longPressTimer);
          longPressTimer = null;
        }
      });

      threeContainer.value.addEventListener("touchmove", () => {
        if (longPressTimer) {
          clearTimeout(longPressTimer);
          longPressTimer = null;
        }
      });
    }
  } catch (error) {
    console.error("MetaverseViewer :", error);
  }
});

// 
const onWindowResize = () => {
  if (metaverse) {
    metaverse.onWindowResize();
  }
};

// 
onBeforeUnmount(() => {
  if (metaverse) {
    metaverse.destroy();
  }

  // 
  document.removeEventListener("visibilitychange", handleVisibilityChange);
});
// 
const toggleDebug = () => {
  debugEnabled.value = !debugEnabled.value;
  if (metaverse) {
    metaverse.setDebugPanelVisible(debugEnabled.value);
    console.log(":", debugEnabled.value ? "" : "");
  }
};

// 
const handleInteraction = () => {
  console.log("", metaverse.playerController.getPosition());

  // @ts-ignore
  window.wx.miniProgram.postMessage({
    data: {
      action: "pay",
      orderId: "12345",
      position: metaverse.playerController.getPosition(),
    },
  });
  // @ts-ignore

  // wx.miniProgram.navigateBack();
  console.log("wx.miniProgram.navigateTo");
  showDialog.value = false;
  closeDialog = true;

  // wx.miniProgram.navigateTo({url: `/pages/authorize/index`});
  wx.miniProgram.navigateTo({ url: `/pages/authorize/index` });
  // 
  // alert("");
};

// 
const restoreSavedPosition = () => {
  if (!metaverse || !metaverse.playerController || !metaverse.sceneController) {
    return;
  }

  metaverse.playerController.restoreSavedPosition(
    metaverse.sceneController,
    "default",
  );
};
</script>

<style scoped>
.three-container {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  /*  */
  padding: 0;
  margin: 0;

  /*  */
  -webkit-touch-callout: none; /* iOS */
  -webkit-user-select: none; /*  */
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  /*  */
  -webkit-context-menu: none;
  -moz-context-menu: none;
  context-menu: none;

  /*  */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;

  /*  */
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  /*  */
  pointer-events: auto;
  touch-action: manipulation; /*  */
}

/*  -  */
@media screen and (max-width: 768px) {
  .three-container {
    /*  */
    margin-top: calc(-1 * env(safe-area-inset-top, 0px));
    margin-bottom: calc(-1 * env(safe-area-inset-bottom, 0px));
    margin-left: calc(-1 * env(safe-area-inset-left, 0px));
    margin-right: calc(-1 * env(safe-area-inset-right, 0px));

    /*  */
    width: calc(
      100vw + env(safe-area-inset-left, 0px) + env(safe-area-inset-right, 0px)
    );
    height: calc(
      100vh + env(safe-area-inset-top, 0px) + env(safe-area-inset-bottom, 0px)
    );

    /*  */
    touch-action: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;

    /*  */
    top: calc(-1 * env(safe-area-inset-top, 0px));
    left: calc(-1 * env(safe-area-inset-left, 0px));
  }
}

/* iPhone  */
@supports (-webkit-touch-callout: none) {
  @media screen and (max-width: 768px) {
    .three-container {
      /* iOS  */
      height: calc(
        100vh + env(safe-area-inset-top, 0px) + env(safe-area-inset-bottom, 0px)
      );
      width: calc(
        100vw + env(safe-area-inset-left, 0px) + env(safe-area-inset-right, 0px)
      );

      /*  transform  */
      transform: translate(
        calc(-1 * env(safe-area-inset-left, 0px)),
        calc(-1 * env(safe-area-inset-top, 0px))
      );
    }
  }
}

/*  */
@media screen and (max-width: 768px) {
  html,
  body {
    overflow: hidden !important;
    margin: 0 !important;
    padding: 0 !important;
    width: 100% !important;
    height: 100% !important;
  }
}

.debug-button {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 8px 16px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  z-index: 100;
  font-size: 14px;
}

.debug-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.save-position-button {
  position: absolute;
  top: 20px;
  right: 120px;
  padding: 8px 16px;
  background-color: rgba(76, 175, 80, 0.8);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  z-index: 100;
  font-size: 14px;
  transition: all 0.2s ease;
}

.save-position-button:hover {
  background-color: rgba(76, 175, 80, 1);
  transform: translateY(-1px);
}

.controls-guide {
  position: absolute;
  bottom: 20px;
  left: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  padding: 12px;
  border-radius: 4px;
  z-index: 100;
  font-size: 14px;
  min-width: 180px;
}

.guide-title {
  font-weight: bold;
  margin-bottom: 8px;
  font-size: 16px;
  text-align: center;
}

.guide-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
}

.key {
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 2px 6px;
  border-radius: 3px;
  margin-right: 10px;
}

.description {
  flex-grow: 1;
  text-align: right;
}

.interaction-button {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  z-index: 100;
}

.interaction-button:hover {
  background-color: #45a049;
  transform: translateX(-50%) scale(1.05);
}

.dialog-container {
  position: absolute;
  z-index: 100;
  pointer-events: none; /* Allow clicks to pass through to the scene */
  left: 50%;
  top: 9%;
  transform: translateX(-50%);
}

.dialog-box {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 15px;
  border-radius: 8px;
  max-width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
  pointer-events: auto; /* Re-enable pointer events for the dialog box */
}

.dialog-text {
  margin: 0 0 15px 0;
  font-size: 16px;
  line-height: 1.4;
}

.purchase-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.2s ease;
  display: block;
  margin: 0 auto;
}

.purchase-button:hover {
  background-color: #45a049;
  transform: scale(1.05);
}
</style>
