import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { InputController } from './controllers/input-controller.js';
import { PlayerController } from './controllers/player-controller.js';
import { SceneController } from './controllers/scene-controller.js';
import DebugPanel from './compontent/debug-panel.js';
import { WindowController } from './controllers/window-controller.js';
import js from '@eslint/js';

export class MetaverseViewer {
  constructor(container, configUrl, options) {
    console.log('container', container, configUrl, options);
    this.configUrl = configUrl;
    this.options = options;
    this.container = container;
    this.clock = null;
    this.animationId = null;
    // 
    this.onInteractionCallback = null;

    // 
    this.landmark = null;
    this.landmarkRing = null;
    this.landmarkAnimTime = 0;

    // 
    this.sceneController = new SceneController(container);
    this.inputController = new InputController(container);
    this.playerController = null;
    this.windowController = new WindowController();
    this.debugPanel = new DebugPanel(this);
  }

  async init() {
    this.config = await this.getSceneMataFile(this.configUrl);
    // 
    this.clock = new THREE.Clock();

    // 
    await this.sceneController.initScene(this.config);

    // 
    this.playerController = new PlayerController(
      this.sceneController.getScene(),
      this.sceneController.getWorldOctree(),
      this.config.avatarInfo,
    );
    await this.playerController.init();
    setTimeout(() => {
      this.config.avatarInfo?.camera
        && this.sceneController.setCameraAndOribitControl(this.config.avatarInfo?.camera);
    }, 300);
    // 
    this.inputController.setCharacterControls(this.playerController);

    // 
    this.inputController.setOrbitCallbacks(
      () => this.startOrbiting(),
      () => this.stopOrbiting(),
    );

    // 
    this.sceneController.initializeCameraTarget(this.playerController.getPosition());

    // 
    this.options?.showInteraction
      && this.options?.geometry
      && this.options.geometryPos
      && this.addInteractionZone({
        geometry: this.options.geometry,
        geometryPos: this.options.geometryPos,
      });

    // 
    this.animate();
    this.animate();
  }

  async getSceneMataFile(url) {
    const requestFile = await fetch(url, {
      mode: 'cors',
      credentials: 'omit',
      cache: 'reload',
    });
    if (requestFile.status !== 200) {
      return new Error(`load ${url} error ${requestFile.statusText},${requestFile.body}`);
    }
    const content = await requestFile.json();
    console.log(`getSceneMataFile success file url ${url} content:`, content);
    return content;
  }
  // 
  addInteractionZone(options) {
    // 
    // const geometry = new THREE.BoxGeometry(4, 2, 4);
    const geometry = new THREE.BoxGeometry(...options.geometry);
    const material = new THREE.MeshBasicMaterial({
      color: 0x00ff00,
      transparent: true,
      opacity: 0.0, // 
      visible: false, // 
    });
    this.interactionZone = new THREE.Mesh(geometry, material);

    // 
    // this.interactionZone.position.set(3, 0, 8);
    this.interactionZone.position.set(...options.geometryPos);

    // 
    this.sceneController.getScene().add(this.interactionZone);

    // 
    this.interactionBox = new THREE.Box3().setFromObject(this.interactionZone);

    // labubu
    console.log('options.geometryPos', options.geometryPos);
    this.addModel(new THREE.Vector3(
      Number(options.geometryPos[0]) - 1.5,
      options.geometryPos[1],
      options.geometryPos[2],
    ));
  }

  // Kana
  addModel(position) {
    const loader = new GLTFLoader();
    loader.setPath('./models/gltf-kara/');

    // 
    loader.load(
      "Kana_Skin.gltf",
      (gltf) => {
        // console.log("Model loaded:====", gltf);
        // const gltfScene = gltf.scene || gltf.scenes[0];
        // gltfScene.position.copy(position);
        // gltfScene.rotation.y = Math.PI; // 
        // // gltf.scene.position.y = 2;
        // // 
        // this.sceneController.getScene().add(gltfScene);
        // // gltf.scene.scale.set(3, 3, 3);
        // this.modelScene = gltfScene;
        const gltfScene = gltf.scene || gltf.scenes[0];
        gltfScene.position.copy(position);
        gltfScene.rotation.y = Math.PI; // 

        // 
        this.sceneController.getScene().add(gltfScene);
        this.modelScene = gltfScene;
        // 
        this.addPointLightsForLabubu();

        // 
        loader.load(
          'AN_Kana_Idle.gltf',
          (gltfIdle) => {
            console.log('Idle animation loaded:', gltfIdle.animations);

            if (gltfIdle.animations && gltfIdle.animations.length > 0) {
              // 
              this.modelMixer = new THREE.AnimationMixer(gltfScene);

              // Idle
              const idleAction = this.modelMixer.clipAction(gltfIdle.animations[0]);
              idleAction.setLoop(THREE.LoopRepeat);
              idleAction.play();
            }
          },
          undefined,
          (error) => {
            console.error('Error loading animation:', error);
          },
        );
      },
      undefined,
      (error) => {
        console.error('Error loading model:', error);
      },
    );
  }

  // labubu
  addPointLightsForLabubu() {
    const ambientLight = new THREE.AmbientLight(0xffffcc, 0.3);
    // 
    this.sceneController.getScene().add(ambientLight);

    // 
    this.modelAmbientLight = ambientLight;
  }

  animate() {
    this.animationId = requestAnimationFrame(this.animate.bind(this));

    const delta = this.clock.getDelta();

    // 
    if (this.playerController && this.playerController.isLoaded()) {
      this.playerController.update(
        delta,
        this.inputController,
        this.sceneController.getCamera(),
      );

      // 
      const playerPosition = this.playerController.getPosition();
      this.sceneController.updateCamera(playerPosition);

      // 
      this.checkInteractionZone(playerPosition);
    }

    // 
    if (this.modelMixer) {
      this.modelMixer.update(delta);
    }

    // 
    this.sceneController.getOrbitControl().update();

    // 
    this.sceneController.animate();
    this.debugPanel.animate();
  }

  // 
  checkInteractionZone(playerPosition) {
    if (!this.interactionZone || !this.interactionBox) return;

    // 
    const playerPoint = new THREE.Vector3(
      playerPosition.x,
      playerPosition.y,
      playerPosition.z,
    );

    // 
    const isInZone = this.interactionBox.containsPoint(playerPoint);

    // 
    if (this.onInteractionCallback) {
      this.onInteractionCallback(isInZone);
    }
  }

  // 
  switchRunToggle() {
    if (this.playerController) {
      this.playerController.switchRunToggle();
    }
  }

  update(delta, keysPressed) {
    // 
    if (keysPressed) {
      this.inputController.keysPressed = keysPressed;
    }

    if (this.playerController && this.playerController.isLoaded()) {
      this.playerController.update(
        delta,
        this.inputController,
        this.sceneController.getCamera(),
      );

      const playerPosition = this.playerController.getPosition();
      this.sceneController.updateCamera(playerPosition);
    }
  }

  destroy() {
    // 
    if (this.animationId) {
      cancelAnimationFrame(this.animationId);
    }

    // 
    this.inputController.destroy();

    // 
    if (this.modelScene) {
      this.sceneController.getScene().remove(this.modelScene);

      this.modelScene = null;
    }

    // 
    if (this.modelMixer) {
      this.modelMixer.stopAllAction();
      this.modelMixer = null;
    }

    // 
    if (this.modelAmbientLight) {
      this.sceneController.getScene().remove(this.modelAmbientLight);
      this.modelAmbientLight.dispose();
      this.modelAmbientLight = null;
    }

    // 
    if (this.sceneController.getRenderer()) {
      this.sceneController.getRenderer().dispose();
    }

    // 
    if (this.windowController) {
      this.windowController.destroy();
    }
  }
  setDebugPanelVisible(visible) {
    if (visible) {
      this.debugPanel.show();
      return;
    }
    this.debugPanel.hide();
  }

  // 
  startOrbiting() {
    if (this.playerController && this.playerController.isLoaded()) {
      const playerPosition = this.playerController.getPosition();
      this.sceneController.startOrbiting(playerPosition);
    }
  }

  // 
  stopOrbiting() {
    this.sceneController.stopOrbiting();
  }

  // 
  setInteractionCallback(callback) {
    this.onInteractionCallback = callback;
  }
  getIsMobile() {
    return this.windowController.getIsMobile();
  }
  onWindowResize() {
    console.log('onWindowResize');
    this.windowController.onWindowResize();
    this.sceneController.onWindowResize();
  }
}
