import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { Octree } from 'three/examples/jsm/math/Octree.js';
// import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { isMobile } from '../utils/index.js';
import { getGaussianSplattingRender } from './render/index.js';

export class SceneController {
  constructor(container) {
    this.container = container;
    this.scene = null;
    this.frameCount = 0;
    this.camera = null;
    this.renderer = null;
    this.orbitControl = null;
    this.worldOctree = null;
    this.cameraTarget = new THREE.Vector3();

    // 
    this.cameraOffset = new THREE.Vector3();
    this.smoothFactor = 0.1;
    this.isFollowingPlayer = true;

    // 
    this.isOrbiting = false;
    this.orbitRadius = 4;
    this.orbitAngle = 0;
    this.orbitSpeed = 0.002; // 1/3
    this.orbitCenter = new THREE.Vector3();
    this.orbitHeight = 0; // 
    this.savedCameraPosition = null; // 
    this.savedCameraTarget = null; // 

    // 
    this.lastPlayerPosition = new THREE.Vector3();
    this.isMobile = isMobile();
    this.offset = this.isMobileVerticalScreen() ? 1 : 0;
  }

  async initScene(config) {
    this.config = config;
    // 
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color(0xa8def0);
    // 
    this.worldOctree = new Octree();
    // 
    this.camera = new THREE.PerspectiveCamera(
      45,
      window.innerWidth / window.innerHeight,
      0.1,
      300,
    );
    this.camera.position.y = 5;
    this.camera.position.z = 5;
    this.camera.position.x = 0;
    this.camera.position.set(0, 2, 10);
    // this.camera.lookAt(0, 2, 0);
    // 
    this.renderer = new THREE.WebGLRenderer({
      antialias: false, // 
      powerPreference: 'high-performance', // 
    });
    this.renderer.setSize(window.innerWidth, window.innerHeight);
    const pixelRatio = this.isMobile
      ? Math.min(1.5, window.devicePixelRatio) // 1.5
      : window.devicePixelRatio;
    this.renderer.setPixelRatio(pixelRatio);
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;

    // DOM
    this.container.appendChild(this.renderer.domElement);

    // 
    this.orbitControl = new OrbitControls(
      this.camera,
      this.renderer.domElement,
    );
    this.orbitControl.enableDamping = true;
    this.orbitControl.minDistance = 4 + this.offset;
    this.orbitControl.maxDistance = 4 + this.offset;
    this.orbitControl.enablePan = false;
    this.orbitControl.maxPolarAngle = Math.PI / 2 + 0.8;
    this.orbitControl.update();

    // 
    this.addLights();

    // 
    // this.loadCollisionWorld();
    this.gsRender = getGaussianSplattingRender(this);
    await this.gsRender.render(this.config, this.container);
    // 
    if (this.config?.avatarInfo?.camera) {
      this.camera.position.set(
        this.config?.avatarInfo?.camera?.position.x,
        this.config?.avatarInfo?.camera?.position.y,
        this.config?.avatarInfo?.camera?.position.z,
      );
      this.camera.lookAt(
        this.config?.avatarInfo?.camera?.target.x,
        this.config?.avatarInfo?.camera?.target.y,
        this.config?.avatarInfo?.camera?.target.z,
      );
    }
    // 
    window.addEventListener('resize', this.onWindowResize.bind(this));
  }

  addLights() {
    const ambientLight = new THREE.AmbientLight(0x6688cc, 0.6);
    this.scene.add(ambientLight);

    //  - 
    const directionalLight = new THREE.DirectionalLight(0xffffff, 1.2);
    directionalLight.position.set(-5, 25, -1);
    directionalLight.castShadow = true;

    // 
    if (this.isMobile) {
      directionalLight.shadow.mapSize.width = 512;
      directionalLight.shadow.mapSize.height = 512;
      directionalLight.shadow.radius = 2;
    } else {
      directionalLight.shadow.mapSize.width = 2048; // 
      directionalLight.shadow.mapSize.height = 2048;
      directionalLight.shadow.radius = 3;
    }

    // 
    directionalLight.shadow.camera.near = 0.1;
    directionalLight.shadow.camera.far = 500;
    directionalLight.shadow.camera.right = 30;
    directionalLight.shadow.camera.left = -30;
    directionalLight.shadow.camera.top = 30;
    directionalLight.shadow.camera.bottom = -30;
    directionalLight.shadow.bias = -0.0001;
    this.scene.add(directionalLight);
  }

  onWindowResize() {
    this.gsRender.onWindowResize();
  }

  animate() {
    // 
    this.updateOrbiting();

    // OrbitControls
    this.adjustCameraDistance();

    this.renderer.render(this.scene, this.camera);
    this.frameCount += 1;
    if (this.frameCount % 2 === 0) this.gsRender.animate();
  }

  adjustCameraDistance() {
    if (!this.orbitControl) return;

    // 
    const polarAngle = this.orbitControl.getPolarAngle();

    // 
    const groundThreshold = Math.PI / 2; // 90
    const maxAngle = Math.PI / 2 + 0.8; // 

    let targetDistance;
    // 
    if (polarAngle <= groundThreshold) {
      // 4
      targetDistance = 4;
    } else {
      // 
      const angleDiff = polarAngle - groundThreshold; // 
      const maxAngleDiff = maxAngle - groundThreshold; // 

      // 01
      const transitionFactor = Math.min(1, angleDiff / maxAngleDiff);

      // 42
      const minDistance = 2;
      const maxDistance = 4 + this.offset;
      targetDistance =        maxDistance - (maxDistance - minDistance) * transitionFactor;
    }

    // 
    this.orbitControl.maxDistance = THREE.MathUtils.lerp(
      this.orbitControl.maxDistance,
      targetDistance,
      0.1, // 
    );

    // 
    const currentDistance = this.orbitControl.getDistance();
    if (currentDistance > this.orbitControl.maxDistance) {
      // 
      const direction = this.camera.position
        .clone()
        .sub(this.orbitControl.target)
        .normalize();
      this.camera.position
        .copy(this.orbitControl.target)
        .add(direction.multiplyScalar(this.orbitControl.maxDistance));
    }
  }
  isMobileVerticalScreen() {
    return this.isMobile && window.innerHeight > window.innerWidth;
  }
  getScene() {
    return this.scene;
  }

  getCamera() {
    return this.camera;
  }

  getRenderer() {
    return this.renderer;
  }

  getOrbitControl() {
    return this.orbitControl;
  }

  getWorldOctree() {
    return this.worldOctree;
  }

  initializeCameraTarget(playerPosition) {
    this.cameraTarget.copy(playerPosition);
    this.cameraTarget.y += 1.7; // 
    this.lastPlayerPosition.copy(playerPosition);
    this.orbitControl.target = this.cameraTarget;
  }
  updateCamera(playerPosition) {
    if (!this.isFollowingPlayer) return;

    // 
    const deltaPosition = playerPosition.clone().sub(this.lastPlayerPosition);

    // 
    this.camera.position.add(deltaPosition);

    // 
    this.cameraTarget.copy(playerPosition);
    this.cameraTarget.y += 1.7; // 
    this.orbitControl.target = this.cameraTarget;

    // 
    this.lastPlayerPosition.copy(playerPosition);
  }

  setCameraAndOribitControl(cameraInfo) {
    const camera = this.getCamera();
    camera.position.set(
      cameraInfo.position.x,
      cameraInfo.position.y,
      cameraInfo.position.z,
    );
    const orbitControl = this.getOrbitControl();

    // 
    orbitControl.target.set(
      cameraInfo.target.x,
      cameraInfo.target.y,
      cameraInfo.target.z,
    );
    orbitControl.azimuthalAngle = cameraInfo.azimuthalAngle;
    orbitControl.polarAngle = cameraInfo.distance;
    orbitControl.distance = cameraInfo.polarAngle;

    // OrbitControls
    orbitControl.update();
  }
  smoothUpdateCamera(playerPosition, delta) {
    if (!this.isFollowingPlayer) return;

    // 
    const targetCameraPosition = playerPosition.clone();
    targetCameraPosition.add(this.cameraOffset);

    this.camera.position.lerp(targetCameraPosition, this.smoothFactor);

    // 
    this.cameraTarget.copy(playerPosition);
    this.cameraTarget.y += 1.7; // 
    this.orbitControl.target.lerp(this.cameraTarget, this.smoothFactor);
  }

  setCameraOffset(offset) {
    this.cameraOffset.copy(offset);
  }

  setFollowPlayer(follow) {
    this.isFollowingPlayer = follow;
  }

  setSmoothFactor(factor) {
    this.smoothFactor = Math.max(0, Math.min(1, factor));
  }

  // 
  enableOrbitControls() {
    this.orbitControl.enabled = true;
  }

  disableOrbitControls() {
    this.orbitControl.enabled = false;
  }

  // 
  startOrbiting(playerPosition) {
    this.isOrbiting = true;
    this.orbitCenter.copy(playerPosition);
    this.orbitCenter.y += 1.7; // 

    // 
    this.savedCameraPosition = this.camera.position.clone();
    this.savedCameraTarget = this.orbitControl.target.clone();

    // 
    const cameraDirection = this.camera.position.clone().sub(this.orbitCenter);
    this.orbitAngle = Math.atan2(cameraDirection.x, cameraDirection.z);
    this.orbitRadius = Math.sqrt(cameraDirection.x * cameraDirection.x
        + cameraDirection.z * cameraDirection.z);
    this.orbitHeight = this.camera.position.y; // 

    // OrbitControls
    this.disableOrbitControls();
  }

  // 
  stopOrbiting() {
    this.isOrbiting = false;

    // OrbitControls
    if (this.savedCameraTarget) {
      this.orbitControl.target.copy(this.savedCameraTarget);
    }

    // OrbitControls
    this.enableOrbitControls();

    // OrbitControls
    this.orbitControl.update();
  }

  // 
  updateOrbiting() {
    if (!this.isOrbiting) return;

    // 
    this.orbitAngle += this.orbitSpeed;

    // 
    const x = this.orbitCenter.x + Math.sin(this.orbitAngle) * this.orbitRadius;
    const z = this.orbitCenter.z + Math.cos(this.orbitAngle) * this.orbitRadius;

    // OrbitControls
    this.camera.position.set(x, this.orbitHeight, z);

    // 
    this.camera.lookAt(this.orbitCenter);
  }

  resetCamera(playerPosition) {
    this.initializeCameraTarget(playerPosition);
  }
  showInfoPanel(visible) {
    this.gsRender.showInfoPanel(visible);
  }
  // 
  updateLighting(time) {
    // 
  }

  updateEnvironment(delta) {
    // 
  }
}
